<template>
  <div class="other">
    <van-nav-bar
        :title="`(${kind})所有题集`"
        fixed
        left-text="返回"
        left-arrow
        @click-left="$router.back()"
    />

    <div style="font-size: 12px;padding: 10px;" v-if="keyword.length > 0">
      关键词：{{keyword}}
    </div>
    <van-search  @search="onSearch" v-model="keyword" placeholder="请输入搜索关键词" />

    <van-cell :title="kind" value="全部结果"/>
    <workbookItem v-for="item in workbookList" :key="item.workbookInfo.id" :workbook-item="item"/>
    <van-divider dashed>{{ workbookList.length === 0 ? '暂无相关题集' : '已经到底了' }}</van-divider>
  </div>
</template>

<script>
import {Toast} from "vant";
import {getWorkBookList} from "@/api";
import workbookItem from "../../../components/workbookItem/workbookItem";
import moment from "moment";

export default {
  name: "workbookList",
  data() {
    return {
      workbookList: [],
      classes: '',
      kind: '',
      keyword: '',
    }
  },
  components: {
    workbookItem
  },
  mounted() {
    let {kind, keyword = ''} = this.$route.query;
    this.kind = kind;
    this.keyword = keyword;
    this.getWorkBookList();
  },
  watch: {
    '$route.query'(){
      let {kind, keyword = ''} = this.$route.query;
      this.kind = kind;
      this.keyword = keyword;
      this.getWorkBookList();
    }
  },
  methods: {
    onSearch(){
      // eslint-disable-next-line no-console
      console.log(this.keyword, this.type);
      this.$router.push(`/user/workbookList?kind=${this.kind}&keyword=${this.keyword}`)
    },
    async getWorkBookList() {
      Toast.loading({
        duration: 0, // 持续展示 toast
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner'
      });
      let {kind} = this;
      let data = await getWorkBookList({
        keyWord: kind, word: this.keyword
      });
      Toast.clear();
      if (data.code === 200) {
        let workbookList = [];
        data.result.workbookList.forEach(item => {
          let {workbookInfo, testList} = item;
          workbookInfo.createdAt = moment(workbookInfo.createdAt).format('YY/MM/DD HH:mm');
          workbookList.push({
            workbookInfo,
            testList
          })
        });
        this['workbookList'] = workbookList;
        // eslint-disable-next-line no-console
        console.log(workbookList);
      } else {
        Toast('获取失败');
      }
    },
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">

</style>
